  defineComponent,
  ref,
} from 'vue'

export default defineComponent({
  name: "WeatherCard",
  props: {
    // 动态数据, 直接使用, 类型参考 WeatherDataType
    weatherInfo: Object,
    weatherInfoState: Boolean
  },

  setup(props) {
    const name = ref("WeatherCard")

    return {
      name,
    }
  }
})

